F<template>
  <div class="set">
  	 <topServer v-if="$route.name==='set'"></topServer>
  	 <div class="set_top">
  	 	  <p class="set_p" v-show="conceal">
  	 	    <Icon type="information-circled" style="margin-left: 0.18rem;"></Icon>
  	 	    <span style="color: #636972;">提现：您的顾客需要线下支付且可能预约时间冲突，你需要自己安排管理预约安排</span>
  	 	    <span @click="conce" style="float: right;margin-right: 0.3rem;cursor: pointer;"><Icon type="close-round"></Icon></span>
  	 	  </p>
  	 	  <div class="set_center">
  	 	  	<ul>
  	 	  		<li>
  	 	  		  <strong>
  	 	  		      信息展示
  	 	  		    <Tooltip placement="right" content="在【基本信息】页面可修改地址与电话信息" class="Tooltip">
                    <Icon type="help-circled" size="18"></Icon>
                 </Tooltip>
  	 	  		  </strong>
  	 	  		  <div style="display: flex; margin-top: 0.05rem;">
  	 	  		    <p>
  	 	  		  	  <input type="checkbox" v-model="site" class="set_check"/> <span style="margin-left: 0.05rem;">显示地址</span>
  	 	  		  	</p>
  	 	  		  	<p style="margin-left: 0.4rem;">
                  <input type="checkbox" v-model="hotLine" class="set_check" /> <span style="margin-left: 0.05rem;">显示服务热线</span>
                </p>
  	 	  		  </div>
  	 	  		</li>
  	 	  		<li style="margin-top: 0.2rem;">
  	 	  		   <strong>
                                  预约按钮名称
                <Tooltip placement="right" content="如：预约看房、预约看车等。" class="Tooltip">
                    <Icon type="help-circled" size="18"></Icon>
                 </Tooltip>
              </strong>
               <div class="input_text">
               	  <input type="text" v-model.trim="name" placeholder="预约看房" />
               </div>
  	 	  		</li>
  	 	  		<li style="margin-top: 0.2rem;">
  	 	  		  <button class="set_button" @click="submit" v-if="!load">保存</button>
  	 	  		  <button class="set_button"  v-if="load">保存 <img src="../../../assets/bai.gif" alt="" style="width:20px;max-height: 100%"></button>
  	 	  		</li>
  	 	  	</ul>
  	 	  </div>
  	 </div>

  </div>
</template>

<script>
   import topServer from '../topServer.vue'
   export default {
    data() {
      return {
        load:false,
        conceal:true,
        hotLine:false,
        site:false,
        name:'',
      }
    },
    methods: {
      submit(){
        this.load = true;
        let site = this.site ? 1 : 0;
        let hotLine = this.hotLine ? 1 : 0;
        this.post("custom/Setting/updateSettingInfo",{
          is_show_address: site,
          is_show_tel: hotLine,
          button_name: this.name
        }).then( res=> {
            this.load = false;
            if (res.data.code === 10000){
              this.$Modal.success({
                title: "设置成功",
                content: res.data.msg
              });
            }else{
              this.$Modal.error({
                title: "设置失败",
                content: res.data.msg
              });
          }
        })
      },
      conce(){
        this.conceal = false
      }
    },
    components: {
      topServer,
    },
    mounted(){
        this.post("custom/Setting/getSettingInfo",{}).then( res=> {
            if(res.data.code === 10000){
              if(res.data.data !== null){
                this.site = res.data.data.is_show_address === 1;
                this.hotLine = res.data.data.is_show_tel === 1;
                this.name = res.data.data.button_name;
              }
            }
        })
    }
  }
</script>

<style scoped="scoped">
  .set_top{
   padding-top: 0.05rem;
   margin-top: 0.2rem;
  padding-left: 0.2rem;
  background: white;
  padding-bottom: 0.2rem;
  box-shadow: 5px 5px 10px #E7E7E7;

  }
  .set_p{
    margin-top: 0.25rem;
    width: 98%;
    height: 0.4rem;
    background: #E9EAED;
    border-radius:0.04rem;
    line-height: 0.4rem;
  }
  .set_center{
    margin-top: 0.26rem;
  }
  .set_center>ul>li{
    margin-bottom: 0.26rem;
  }
  .set_center>ul>li>strong{
    font-size: 0.15rem;
  }
  .set_check{
    position: relative;
    top: 1px;
  }
  .input_text{
    margin-top: 0.1rem;
  }
  .input_text>input{
    width: 1.8rem;
    border: 1px solid #999999;
    height: 0.35rem;
    padding-left: 0.04rem;
    border-radius:0.04rem;
  }
  .set_button{
    margin-top: 0.1rem;
    width: 0.8rem;
    height: 0.35rem;
    color: #F8FFFF;
    border: none;
    font-size: 0.16rem;
    background: #59A2ED;
    border-radius:0.04rem;
   }
</style>



